.order-details .paidLogo {
  font-weight: bold;
  position: absolute;
  top: 40px;
  right: 60px;
  font-family: 'Helvetica', serif;
  color: rgba(211, 65, 50, 0.5);
  font-size: 48px;
  -ms-transform: rotate(-20deg); /* IE 9 */
  -webkit-transform: rotate(-20deg); /* Safari */
  transform: rotate(-20deg);
}

@media only screen and (max-width: 768px) {
  .commerceordersedit #action-button {
    flex-direction: column-reverse;
    height: auto;

    & > .flex {
      flex-wrap: wrap;
      width: 100%;
    }
  }

  body.ltr.commerceordersedit #action-button.flex > *:not(:last-child) {
    margin-right: 0 !important;
  }
}

#paymentmodal.loading {
  min-width: 300px;
  min-height: 300px;
}

#paymentmodal.loading {
  background: url(../images/spinner_big.gif) no-repeat 50% 50%;
}

#paymentmodal fieldset {
  margin: 24px 0;
}

#paymentmodal fieldset legend {
  margin-bottom: 4px;
}

.order-info table.borderless td {
  border: none !important;
}

.transaction-status.transaction-status-failed {
  color: #D0021B;
}

.transaction-status.transaction-status-success {
  color: #27AE60;
}

.gateway-response {
  display: block;
  min-width: 250px;
  max-width: 400px;
  min-height: 50px;
  max-height: 400px;
  font-family: Menlo, monospace;
  font-size: 0.9em !important;
  overflow: auto;
  outline: 1px solid #ccc;
  padding: 5px;
  margin: 5px;
}

.detailHud code {
  font-family: Menlo, monospace;
}

.transaction-refund input[type="text"] {
  width: 100%;
  margin-bottom: 10px;
}

.transaction-refund a {
  float: right;
}
